html {
  font-size: 16px;
}
html {
  font-size: 1rem;
}
@media screen and (max-width: 1920px) {
  html {
    font-size: 1rem;
  }
}
@media screen and (max-width: 768px) {
  html {
    font-size: 0.875rem;
  }
}

.nav_scroll_shadow {
  box-shadow: 0px 10px 40px -20px #5b6686;
  transition: all 0.7s;
}
.home_hero_rocket_img {
  animation: rocketAnim 2s linear infinite;
}
@keyframes rocketAnim {
  0% {
    top: 50%;
  }
  50% {
    top: 55%;
  }
  100% {
    top: 50%;
  }
}

@keyframes scroll {
  from {
    transform: translateX(0);
  }
  to {
    transform: translateX(calc(-100% - 1rem));
  }
}

.scroll {
  animation: scroll 60s linear infinite;
}
@media screen and (max-width: 768px) {
  .scroll {
    animation: scroll 60s linear infinite;
  }
}
.reverse {
  animation-direction: reverse;
}
.blog_card_brief {
  display: -webkit-box;
  overflow: hidden;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 3;
}
.blog_card {
  filter: drop-shadow(0px 10px 10px rgba(128, 128, 128, 0.3));
  transition: filter 0.5s ease;
}
.blog_card:hover {
  filter: drop-shadow(0px 10px 10px rgba(96, 97, 194, 0.5));
  transition: filter 0.5s ease;
}
.swiper-pagination {
  position: relative;
}
.swiper-horizontal > .swiper-pagination-bullets,
.swiper-pagination-bullets.swiper-pagination-horizontal,
.swiper-pagination-custom,
.swiper-pagination-fraction {
  top: 10px;
}
.swiper-pagination-bullet {
  width: 20px;
  height: 5px;
  border-radius: 0;
}
.swiper-pagination-bullet-active {
  background: #3b56a5;
}
.wage_list_item::before {
  content: url('https://moocads.com/img/cdap/check.svg');
  position: absolute;
  height: 19px;
  width: 15px;
  top: 0;
  left: -15px;
}
.icon_wrap {
  border-radius: 15px;
  transition: all 0.2s ease-in-out;
}
.icon_wrap:hover,
.icon_wrap.active {
  box-shadow: 0px 10px 40px -16px #9bafeb;
  cursor: pointer;
}
.home_industry_content.active {
  display: block;
}
.service_steps_wrap::after {
  content: '';
  background-repeat: no-repeat;
  background-size: 25%;
  background-position: 0 center;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  opacity: 0.6;
}
.recent_blog_title {
  display: -webkit-box;
  overflow: hidden;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 1;
}
.recent_blog_title:hover {
  -webkit-line-clamp: 3;
}
